import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

const Search = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [movieData, setMovieData] = useState([]);
  const apiKey = '6a8f5f0f';

  const fetchMovieData = async () => {
    try {
      const response = await axios.get(`https://www.omdbapi.com/?apikey=${apiKey}&s=${searchTerm}`);
      if (response.data.Search) {
        setMovieData(response.data.Search);
      } else {
        setMovieData([]);
      }
    } catch (error) {
      console.error('Error fetching movie data:', error);
      setMovieData([]);
    }
  };

  useEffect(() => {
    if (searchTerm) {
      fetchMovieData();
    } else {
      setMovieData([]);
    }
  }, [searchTerm]);

  return (
    <>
      <div className="SearchPage">
        <div className="container py-5">
          <section className="py-5 text-center container" id="SearchContent" style={{ boxShadow: '0 0 30px #c06c84' }}>
            <div className="row py-lg-5">
              <div className="col-lg-6 col-md-8 mx-auto">
                <h1 className="fw-bold my-5" style={{ color: '#113f67' }}>
                  "Discover, Watch, Enjoy  Your Movie Search Begins Here!"
                </h1>

                <form className="row g-3">
                  <div className="col-auto">
                    <input
                      type="text"
                      className="form-control"
                      id="exampleFormControlInput1"
                      placeholder="Search Movie"
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      style={{ color: "#fcff82",backgroundColor:'#3baea0',border:'2px solid white' }}
                    />
                  </div>
                  <div className="col-auto">
                    <button type="submit" id="SearchBtn" className="btn btn-primary mb-3">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="20"
                        height="20"
                        fill="currentColor"
                        className="bi bi-search me-2"
                        viewBox="0 0 16 16"
                      >
                        <path
                          d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"
                        />
                      </svg>
                      Search
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </section>
        </div>

        <div className="container">
          <div className="row">
            {movieData.length === 0 ? (
              <div className="col-12 text-center">
                <p style={{color:'white'}}> <svg id='SadEmoji' xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" class="bi bi-emoji-frown" viewBox="0 0 16 16">
  <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
  <path d="M4.285 12.433a.5.5 0 0 0 .683-.183A3.498 3.498 0 0 1 8 10.5c1.295 0 2.426.703 3.032 1.75a.5.5 0 0 0 .866-.5A4.498 4.498 0 0 0 8 9.5a4.5 4.5 0 0 0-3.898 2.25.5.5 0 0 0 .183.683zM7 6.5C7 7.328 6.552 8 6 8s-1-.672-1-1.5S5.448 5 6 5s1 .672 1 1.5zm4 0c0 .828-.448 1.5-1 1.5s-1-.672-1-1.5S9.448 5 10 5s1 .672 1 1.5z"/>
</svg>  No Data</p>
              </div>
            ) : (
              movieData.map((movie) => (
                <div className="col-md-4" key={movie.imdbID}>
                  <div className="card mb-4">
                    <Link to={`/${movie.Title}`}>
                      <img
                        src={movie.Poster}
                        className="card-img-top"
                        alt={movie.Title}
                        style={{ height: '30vh' }}
                        onError={(e) => {
                          e.target.src = 'https://th.bing.com/th/id/OIP.DAuF8ksdA5Kjh7fLifDpnwHaHa?pid=ImgDet&rs=1';
                        }}
                      />
                    </Link>
                    <div className="card-body">
                      <h3 className="card-title">{movie.Title}</h3>
                      <p className="card-text">{movie.Plot}</p>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Search;
